import React from "react";

function Table({lista}) {
  const item = lista.map(element => Object.entries(element))
  const tittle = item[0].map(([key,value]) => key.toString().charAt(0).toUpperCase() + key.toString().slice(1))
  tittle[0] = "#"

  return (
    <div className="appTable">
      <table className="table">
        <thead className="fixed">
          <tr>
            {tittle.map((element,i) => <th key={i}>{element}</th>)}
          </tr>
        </thead>
        <tbody>
          {item.map((element,i) => (
            <tr key={i}>
              {element.map( ([key,value]) => (
                <td>{value}</td>
              ))}
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}

export default Table;
