import axios from 'axios';

const token = window.localStorage.getItem('token');

const config = {
  headers: {
    'Authorization': 'Bearer ' + token
  }
}

export const listTurnos = async () => {
  try {
    const r = await axios.get('http://localhost:8080/turnos', config);
    const { data } = r;
    return [null, data];
  } catch (error) {
    return [error, null];
  }
}

export const crearTurno = async (payload) => {
  try {
    const r = await axios.post('http://localhost:8080/turnos/crear', payload, config);
    const { data } = r;
    return [null, data];
  } catch (error) {
    return [error, null];
  }
}

