import React from 'react'
import { authLogin } from './../../service/AuthService'
import { useForm } from "react-hook-form";
import { useContext } from 'react'
import { UserContext } from './../../context/UserContext'
import { Form, Input, Button, TextUp } from "./../../components/form/formulario";

function LoginForm({handleAccion, accion}) {
  const { user, setUser } = useContext(UserContext);

  async function dataUser(d) {
    const [error,data] = await authLogin(d);
    if(error) {
      console.log(error);
      return;
    }
    setUser(data);
    window.localStorage.setItem('token', data.token);
  }  
  
  return (
    <div className={`login ${accion}`}>
      <Form onSubmit={dataUser} tittle="Iniciar sesión">
        <TextUp label="¿Necesitas una cuenta? " ancor="Registrarse" action={handleAccion}/>
        <Input type="text" label="Usuario" name="nombreUsuario" />
        <Input type="password" label="Contraseña" name="password" />
        <Button type="submit" label="Iniciar sesion"/>
      </Form>
    </div>
  )
}

export default LoginForm