import UserProvider, { UserContext } from './context/UserContext';
import Login from './pages/login/Login';
import Home from './pages/home/Home';

function App() {
  return (
    <UserProvider>
      <UserContext.Consumer>
        {({user}) => user ? <Home /> : <Login /> }
      </UserContext.Consumer>
    </UserProvider>
  );
}

export default App;
