import axios from 'axios';

const token = window.localStorage.getItem('token');
const config = {
  headers: {
    'Authorization': 'Bearer ' + token
  }
}

export const listPacientes = async () => {
  try {
    const r = await axios.get('http://localhost:8080/pacientes', config);
    const { data } = r;
    return [null, data];
  } catch (error) {
    return [error, null];
  }
}

export const crearPaciente = async (payload) => {
  try {
    const r = await axios.post('http://localhost:8080/pacientes/crear', payload, config);
    const { data } = r;
    return [null, data];
  } catch (error) {
    return [error, null];
  }
}




