
import Navbar from './../../components/navbar/Navbar';
import Odontologo from '../odontologo/Odontologo';
import Paciente from '../paciente/Paciente';
import Info from '../info/Info';
import Turno from '../turno/Turno';
import './home.scss'
import img from './../../assets/welcome.svg'
import { useState, useEffect } from 'react';
import { BrowserRouter, Routes, Route } from "react-router-dom";


function Home(props) {
  const [path, setPath] = useState('')

  const updatePath = () => {
    setTimeout(() => {
      setPath(window.location.pathname)
    },0) 
  }
  
  useEffect(() => {
    updatePath()
  } , [path])

  return (
    <>
      <BrowserRouter>
        <Navbar handlePath={updatePath}/>
        <div className="contenedor">
          <Routes>
            <Route path='/odontologo' element={<Odontologo/>}/>
            <Route path="/paciente" element={<Paciente/>}/>
            <Route path="/turnos" element={<Turno/>}/>
          </Routes>
          {path == "/" && <img src={img} alt="welcome" className="welcome"/>}
        </div>
      </BrowserRouter>
    </>
  )
}

export default Home;