import { listPacientes, crearPaciente } from './../../service/PacienteService'
import { Form, Input, Button, Input2 } from "./../../components/form/formulario";
import React, { useState, useEffect, useRef } from 'react'
import Table from './../../components/table/Table'

function Paciente() {
  const [pacientes, setPacientes] = useState([])
  const formulario = useRef(null)
  const inputFocus = useRef(null)

  useEffect(() => {
    lista()
  }, [])

  async function lista() {
    const [error,data] = await listPacientes();
    if(error) {
      console.log(error);
      return;
    }
    setPacientes(data);    
  }

  console.log(pacientes);

  async function crear(d) {
    console.log(d);
    const [error,data] = await crearPaciente(d);
    if(error) {
      console.log(error);
      return;
    }
    lista()
    formulario.current.reset()
    console.log(inputFocus.current);
    //inputFocus.current.focus()
  }  

  return (
    <div className='odonCss'>
      <div className="odontologos">
        <Form onSubmit={crear} tittle="Crear paciente" reference={formulario}>
          <Input2 type="text" label="Nombre" name="nombre" type2="text" label2="Apellido" name2="apellido"/>
          <Input2 type="text" label="DNI" name="dni" type2="date" label2="Fecha de ingreso" name2="fechaIngreso"/>
          <Input type="text" label="Email" name="email"/>
          <Input2 type="text" label="Calle" name="calle" type2="text" label2="Numero" name2="numero"/>
          <Input type="text" label="Localidad" name="localidad"/>
          <Input type="text" label="Provincia" name="provincia"/>
          <Button type="submit" label="Crear"/>
        </Form>
      </div>
      <div className="listaOdontologos">
        {pacientes.length > 0 ? <Table lista={pacientes}/> : <p>No hay pacientes</p>} 
      </div>
    </div>
  )
}

export default Paciente