import './odontologo.scss'
import { listOdontologos, crearOdontologo } from './../../service/OdontologoService'
import { Form, Input, Button } from "./../../components/form/formulario";
import React, { useState, useEffect, useRef } from 'react'
import Table from './../../components/table/Table'

function Odontologo() {
  const [odontologos, setOdontologos] = useState([])
  const formulario = useRef(null)
  const inputFocus = useRef(null)

  useEffect(() => {
    lista()
  }, [])

  async function lista() {
    const [error,data] = await listOdontologos();
    if(error) {
      console.log(error);
      return;
    }
    setOdontologos(data);    
  }

  async function crear(d) {
    console.log(d);
    const [error,data] = await crearOdontologo(d);
    if(error) {
      console.log(error);
      return;
    }
    lista()
    formulario.current.reset()
    console.log(inputFocus.current);
    //inputFocus.current.focus()
  }  

  return (
    <div className='odonCss'>
      <div className="odontologos">
        <Form onSubmit={crear} tittle="Crear odontologo" reference={formulario}>
          <Input type="text" label="Nombre" name="nombre" refReact={inputFocus}/>
          <Input type="text" label="Apellido" name="apellido" />
          <Input type="text" label="Matricula" name="matricula" />
          <Button type="submit" label="Crear"/>
        </Form>
      </div>
      <div className="listaOdontologos">
        {odontologos.length > 0 ? <Table lista={odontologos}/> : <p>No hay odontologos</p>} 
      </div>
    </div>
  )
}

export default Odontologo