import React from 'react'
import { authRegister } from '../../service/AuthService'
import { Form, Input, Button, TextUp } from "./../../components/form/formulario";

function RegisterForm({handleAccion, accion}) {

  async function registerUser(d) {
    const [error,data] = await authRegister(d);
    if(error) {
      console.log(error);
      return;
    }
    console.log(data);
  } 
  

  return (
    <div className={`sign_in ${accion}`}>
      <Form onSubmit={registerUser} tittle="Registro">
        <TextUp label="Ya tienes una cuenta ? " ancor="Iniciar sesión" action={handleAccion}/>
        <Input type="text" label="Nombre" name="nombre" />
        <Input type="text" label="Email" name="email" />
        <Input type="text" label="Nombre de usuario" name="nombreUsuario" />
        <Input type="password" label="Contraseña" name="password" />
        <Button type="submit" label="Crear"/>
      </Form>
    </div>
  )
}

export default RegisterForm