import axios from 'axios';

export const authLogin = async (payload) => {
  try {
    const r = await axios.post('http://localhost:8080/auth/login', payload)
    const { data } = r;
    return [null, data];
  } catch (error) {
    return [error.response.status, null];
  }
}

export const authRegister = async (payload) => {
  try {
    const r = await axios.post('http://localhost:8080/auth/nuevo', payload)
    const { data } = r;
    return [null, data];
  } catch (error) {
    return [error.response.status, null];
  }
}


