import { Link } from 'react-router-dom';
import UserProvider, { UserContext } from './../../context/UserContext';
import { useContext } from 'react';
import './navbar.scss';

function Navbar({handlePath}) {
  const { user, setUser } = useContext(UserContext);
  const permiso = user.authorities.length>1

  return(
    <div className="navbar">
      <div className='navbar_menu'>
        <ul>         
          {permiso && <li><Link to="/odontologo" onClick={handlePath}>Odontologos</Link></li>}
          {permiso && <li><Link to="/paciente" onClick={handlePath}>Pacientes</Link></li>}
          <li><Link to="/turnos" onClick={handlePath}>Turnos</Link></li>
        </ul>
      </div>
      <div className='navbar_user'>
        <p>Hola, <span>{user.nombreUsuario[0].toUpperCase() + user.nombreUsuario.slice(1)}</span></p>
        <div className='circle_profile'>
          <p>{user.nombreUsuario[0].toUpperCase()}</p>
        </div>
      </div>
    </div>
  )
}

export default Navbar
