import "./login.scss";
import { useState, useEffect } from "react";
import LoginForm from "./LoginForm";
import RegisterForm from "./RegisterForm";


function Login() {
  const [accion, setAccion] = useState("Dlogin");

  const handleAccion = (e) => {
    e.preventDefault()
    accion === "Dlogin" ? setAccion("Dregister") : setAccion("Dlogin");
  };

  return (
    <div className="content">
      <LoginForm handleAccion={handleAccion} accion={accion}/>
      <RegisterForm handleAccion={handleAccion} accion={accion}/>
      <div className={`divisor ${accion}`} id="divisor">
      </div>
    </div>
  );
}

export default Login;
