import React from "react";
import { useForm } from "react-hook-form";

export function Form({ defaultValues, children, onSubmit, tittle, reference }) {
  const { handleSubmit, register } = useForm({ defaultValues });

  return (
    <form onSubmit={handleSubmit(onSubmit)} className="formAuth" ref={reference}>
      <h4>{tittle}</h4>
      {Array.isArray(children)
        ? children.map((child) => {
            return child.props.name
              ? React.createElement(child.type, {
                  ...{
                    ...child.props,
                    register,
                    key: child.props.name
                  }
                })
              : child;
          })
        : children}
    </form>
  );
}

export function TextUp({ label, ancor, action }){
  return (
    <div className="row">
      <div className="col-4"></div>
      <div className="col-8 d-flex align-items-end justify-content-end">
        <p>
          {label}
          <a href="true" onClick={action}>{ancor}</a>
        </p>
      </div>
    </div>
  )
}

export function Input({ type, refReact, label, register, name, ...rest}) {
  return (
    <div className="form-floating mb-3">
      <input ref={refReact} type={type} className="form-control" placeholder={label} {...register(name)}/>
      <label htmlFor="floatingInput">{label}</label>
    </div>
  )
}

export function Input2({ type, type2, refReact, label, label2, register, name, name2, ...rest}) {
  return (
    <div className="row">
      <div className="form-floating mb-3 col-6">
        <input ref={refReact} type={type} className="form-control" placeholder={label} {...register(name)}/>
        <label htmlFor="floatingInput">{label}</label>
      </div>
      <div className="form-floating mb-3 col-6">
        <input ref={refReact} type={type2} className="form-control" placeholder={label2} {...register(name2)}/>
        <label htmlFor="floatingInput">{label2}</label>
      </div>
    </div>
  )
}

export function Select({ register, options, name, label, ...rest }) {
  return (
    <div className="form-floating mb-3">
      <select className="form-select" id="floatingSelect" aria-label="Floating label select example" {...register(name)} {...rest}>
        <option selected disabled>Seleccionar</option>
        {options.map((value,i) => (
          <option key={i} value={value}>{value}</option>
        ))}
      </select>
      <label htmlFor="floatingSelect">{label}</label>
    </div>
  )
}

export function Button({ label, type}) {
  return (
    <div className="d-grid gap-2">
      <button className="btn_forms" type={type}>
        {label}
      </button>
    </div>
  );
}

