import { Form, Input, Button, Input2, Select } from "./../../components/form/formulario";
import { listTurnos, crearTurno } from "./../../service/TurnoService";
import { listOdontologos } from './../../service/OdontologoService'
import { listPacientes } from './../../service/PacienteService'
import React, { useState, useEffect, useRef } from 'react'
import Table from './../../components/table/Table'


function Turno() {
  const [turnos, setTurnos] = useState([])
  const [odontologos, setOdontologos] = useState([])
  const [pacientes, setPacientes] = useState([])
  const formulario = useRef(null)

  useEffect(() => {
    lista()
    listaOdontologos()
    listaPacientes()
  }, [])

  async function crear(d) {
    const [error1,listPaciente] = await listPacientes();
    const [error2,listOdontologo] = await listOdontologos();
    const [nompreP, apellidoP] = d.paciente.split(' ')
    const [nompreO, apellidoO] = d.odonologo.split(' ')
    const pSelect = listPaciente.find(e => { return e.nombre == nompreP && e.apellido == apellidoP })
    const oSelect = listOdontologo.find(e => { return e.nombre == nompreO && e.apellido == apellidoO })
    const armado = {
      fecha: d.fecha,
      paciente: pSelect,
      odontologo: oSelect
    }
    const [error,data] = await crearTurno(armado);
    if(error) {
      console.log(error);
      return;
    } 
    lista()
    formulario.current.reset()
  }

  async function lista() {
    const [error,data] = await listTurnos();
    if(error) {
      console.log(error);
      return;
    }
    const armado = data.map(e =>{
      return {
        id: e.id,
        fecha: e.fecha,
        odontologo: e.odontologo.nombre + " " + e.odontologo.apellido,
        paciente: e.paciente.nombre + " " + e.paciente.apellido
      }
    })
    setTurnos(armado);
  }

  async function listaOdontologos() {
    const [error,data] = await listOdontologos();
    if(error) {
      console.log(error);
      return;
    }
    const names = data.map(odontologo => odontologo.nombre + ' ' + odontologo.apellido)
    setOdontologos(names);
  }

  async function listaPacientes() {
    const [error,data] = await listPacientes();
    if(error) {
      console.log(error);
      return;
    }
    const names = data.map(paciente => paciente.nombre + ' ' + paciente.apellido)
    setPacientes(names);
  }

  return (
    <div className='odonCss'>
      <div className="odontologos">
        <Form onSubmit={crear} tittle="Tomar un turno" reference={formulario}>
          <Input type="date" name="fecha" label="fecha"/>
          <Select options={odontologos} name="odonologo" label="Odontologos"/>
          <Select options={pacientes} name="paciente" label="Odontologos"/>
          <Button label="Generar" type="submit"/>
        </Form>
      </div>
      <div className="listaOdontologos">
        {turnos.length > 0 ? <Table lista={turnos}/> : <p>No se encuentra ningun turno registrado</p>}
      </div>
    </div>
  )
}

export default Turno